/* Category 08 is for OuterlooperPress design elements */
body {
  position: relative;
}

#page {
  position: relative;
  z-index: 0;
}

#masthead {
  margin: 0;
  width: auto;
  min-width: 500px;
  max-width: auto;
  display: block;
  position: fixed;
  left: 5px;
  top: 0px;
  transform: rotate(90deg);
  transform-origin: bottom left;
  text-align: left;
  text-transform: uppercase;
  white-space: nowrap;
  z-index: 777;
}

.site-header {
  padding: 0;
  width: auto;
}

.site-title {
  color: rgba(0, 0, 0, 0.35);
  display: inline-block;
  font-size: 2.639rem;
  line-height: normal;
  margin: 0 11px 0 0;
  padding: 0;
  padding-top: 0 !important;
  -webkit-text-stroke: 1px #fff;
  -webkit-box-reflect: below -18px linear-gradient(transparent, rgba(0, 0, 0, 0.3333333333));
  transition: 0.5s;
}
.site-title a {
  display: inline-block;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.35);
  -webkit-text-stroke: 1px #fff;
  transition: 0.5s;
}
.site-title a:hover, .site-title a:active, .site-title a:focus {
  text-decoration: none;
  filter: drop-shadow(-3px -3px 5px rgb(255, 0, 255)) drop-shadow(5px 5px 10px rgba(255, 0, 255, 0.88));
}
.site-title .site-description {
  color: #fff;
  display: inline-block;
  font-size: 1.111rem;
  font-weight: 400;
  line-height: normal;
  margin: 0;
  padding: 0;
  -webkit-text-stroke: 0;
}

.stars, .twinkling, .clouds {
  position: absolute;
  position: fixed;
  display: block;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
}

.stars {
  z-index: -3;
  background: #000 url("assets/images/stars/stars.png") repeat top center;
}

.twinkling {
  z-index: -2;
  background: transparent url("assets/images/stars/twinkling.png") repeat top center;
  animation: move-twink-back 200s linear infinite;
}

@keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}
@keyframes move-clouds-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 10000px 0;
  }
}
/* Animated Starfield 
------------------------------------- */
#starfield-space, .starfield-stars {
  overflow: hidden;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

.starfield-stars {
  background-image: radial-gradient(2px 2px at 20px 30px, #eee, rgba(0, 0, 0, 0)), radial-gradient(2px 2px at 40px 70px, violet, rgba(0, 0, 0, 0)), radial-gradient(2px 2px at 50px 160px, #ddd, rgba(0, 0, 0, 0)), radial-gradient(2px 2px at 90px 40px, #fff, rgba(0, 0, 0, 0)), radial-gradient(2px 2px at 130px 80px, violet, rgba(0, 0, 0, 0)), radial-gradient(2px 2px at 160px 120px, #ddd, rgba(0, 0, 0, 0));
  background-repeat: repeat;
  background-size: 200px 200px;
  animation: zoom 5s infinite;
  opacity: 0;
}

.starfield-stars:nth-child(1) {
  background-position: 50% 50%;
  animation-delay: 0s;
}

.starfield-stars:nth-child(2) {
  background-position: 20% 60%;
  animation-delay: 1s;
}

.starfield-stars:nth-child(3) {
  background-position: -20% -30%;
  animation-delay: 2s;
}

.starfield-stars:nth-child(4) {
  background-position: 40% -80%;
  animation-delay: 3s;
}

.starfield-stars:nth-child(5) {
  background-position: -20% 30%;
  animation-delay: 4s;
}

@keyframes zoom {
  0% {
    opacity: 0;
    transform: scale(0.5);
    animation-timing-function: ease-in;
  }
  85% {
    opacity: 1;
    transform: scale(2.8);
    animation-timing-function: linear;
  }
  100% {
    opacity: 0;
    transform: scale(3.5);
  }
}